import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Index from '../components/pages/frontend/index.vue'
import Product from '../components/pages/frontend/product.vue'
import Login from '../components/pages/frontend/login.vue'
import Content from '../components/pages/frontend/content.vue'
import Dashboard from '../views/dashboard.vue';
import DashboardIndex from '../components/pages/backend/dashboardIndex.vue'
import ProductManage from '../components/pages/backend/productManage.vue'
import Checkout1 from '../components/pages/frontend/checkout1.vue'
import Checkout2 from '../components/pages/frontend/checkout2.vue'
import CheckoutSuccess from '../components/pages/frontend/checkoutSuccess.vue'
import Coupon from '../components/pages/backend/coupon.vue'
import Order from '../components/pages/backend/order.vue'
import Favorite from '../components/pages/frontend/favorite.vue'
import ReportDay from '../components/pages/backend/reportDay.vue'
import ReportWeek from '../components/pages/backend/reportWeek.vue'
import ReportMonth from '../components/pages/backend/reportMonth.vue'

Vue.use(VueRouter)

const routes = [
  {
    path:'*',
    redirect:'/'
  },

    {
    path: '/',
    component: Home,
    children:[
      {
        path: '/',
        name: 'Index', 
        component: Index, 
        meta: {
          breadcrumb: [{
            name: '首頁'
          }]
        } 
      },
      {
        path: '/product/:kind?',
        name: 'Product',
        component: Product,  
        meta: {
          breadcrumb: [{
            name: '首頁',
            link: '/'
          },
          {
            name: '商品'
          }]
        }
      },
      {
        path: '/login',
        name: 'Login',
        component: Login,
        meta:{
          breadcrumb:[{
            name: '首頁',
            link: '/'
          },{
            name: '管理員登入',
         
        }]
      }},
      {
        path:'/content/:contentId',
        name:'Content',
        component:Content,
        meta:{
          breadcrumb:[{
            name: '首頁',
            link: '/'
          },{
            name: '所有商品',
            link:'/product'
          },{
            name:'商品內容'
          }]
        }

      },
      {
        path: '/favorite',
        name: 'Favorite',
        component: Favorite,  
        meta: {
          breadcrumb: [{
            name: '首頁',
            link: '/'
          },
          {
            name: '我的收藏'
          }]
        }
      },
      {
        path:'/checkoutCoupon',
        name:'Checkout1',
        component:Checkout1,
        meta:{
          breadcrumb:[{
            name: '首頁',
            link: '/'
          },{
            name: '結帳-訂單資料確認',
          }]
        }
      },
      {
        path:'/checkoutPay/:orderId',
        name:'Checkout2',
        component:Checkout2,
        meta:{
          breadcrumb:[{
            name: '首頁',
            link: '/'
          },{
            name: '結帳-付款',
          }]
        }

      },
      {
        path:'/checkoutSuccess',
        name:'CheckoutSuccess',
        component:CheckoutSuccess,
        meta:{
          breadcrumb:[{
            name: '首頁',
            link: '/'
          },{
            name: '付款成功',
          }]
        }

      }
    ]
  },
  {
    path: '/admin',
    name: 'Dashboard',
    component: Dashboard,
 
    children:[
      {
        path: 'dashboard',
        name: 'DashboardIndex',
        component: DashboardIndex,
        meta: { requiresAuth: true },
      } ,
      {
        path: 'productManage',
        name: 'ProductManage',
        component: ProductManage,
        meta: { requiresAuth: true },
      } ,
      {
        path: 'coupon',
        name: 'Coupon',
        component: Coupon,
        meta: { requiresAuth: true },
      } ,
      {
        path: 'order',
        name: 'Order',
        component: Order,
        meta: { requiresAuth: true },
      } ,
      {
        path: 'reportDay',
        name: 'ReportDay',
        component: ReportDay,
        meta: { requiresAuth: true },
      } ,
      {
        path: 'reportWeek',
        name: 'ReportWeek',
        component: ReportWeek,
        meta: { requiresAuth: true },
      } ,
      {
        path: 'reportMonth',
        name: 'ReportMonth',
        component: ReportMonth,
        meta: { requiresAuth: true },
      } ,
    ]
  }
]

const router = new VueRouter({
  linkExactActiveClass:'active',
  routes
})

export default router
